.blogs-box {
  padding: 100px 0;
  display: flex;
  justify-content: space-between;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: 0.7s ease;
  transition-delay: 0.3s;
}

.blogs-box.is-inview {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.blogs-box .top_blog {
  width: 80%;
  margin: 0 auto;
  padding: 30px;
  background-color: #fff;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
}

.blogs-box .top_blog .ttl-Box .ttl {
  padding-bottom: 5px;
  color: #333;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 1.6em;
  letter-spacing: 0.1em;
  border-bottom: 3px solid #ccc;
  position: relative;
}

.blogs-box .top_blog .ttl-Box .ttl::after {
  content: '';
  display: block;
  width: 160px;
  height: 4px;
  background-color: #e45213;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.blogs-box .top_blog .scroll_box {
  box-sizing: border-box;
  overflow-y: scroll;
  transform: translateZ(0);
  height: 267px;
}

.blogs-box .top_blog .news-list__outline {
  border-bottom: 1px solid #ccc;
  padding: 15px 0;
}

.blogs-box .top_blog .news-list__outline .news-list__left {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.blogs-box .top_blog .news-list__outline .news-list__left .post-date {
  font-size: 1.4rem;
}

.blogs-box .top_blog .news-list__outline .news-list__left .list-title {
  width: 100%;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 8px 0;
}

.blogs-box .top_blog .news-list__outline .news-list__left .list-title a {
  text-decoration: none;
}

.blogs-box .top_blog .news-list__outline .post-categories a {
  font-size: 1.3rem;
  text-decoration: none;
  display: inline-block;
  background: #e45213;
  padding: 2px 5px 3px;
  color: #fff;
  margin-right: 10px;
}

.blogs-box .top_blog .news-list__outline .news-list__right p {
  font-size: 1.4rem;
  line-height: 1.4em;
}

@media screen and (max-width: 767px) {
  .blogs-box {
    padding: 10.66667vw 0;
    display: block;
  }
  .blogs-box .top_blog {
    width: 100%;
    margin-bottom: 5.33333vw;
    padding: 5.33333vw;
    box-shadow: 0.8vw 0.8vw 2.66667vw rgba(0, 0, 0, 0.3);
  }
  .blogs-box .top_blog:last-of-type {
    margin-bottom: 0;
  }
  .blogs-box .top_blog .ttl-Box .ttl {
    padding-bottom: 1.33333vw;
    font-size: 2.2rem;
  }
  .blogs-box .top_blog .ttl-Box .ttl::after {
    width: 37.33333vw;
  }
  .blogs-box .top_blog .scroll_box {
    height: 100vw;
  }
  .blogs-box .top_blog .news-list__outline {
    padding: 4vw 0;
  }
  .blogs-box .top_blog .news-list__outline .news-list__left .list-title {
    margin: 2.13333vw 0;
  }
  .blogs-box .top_blog .news-list__outline .post-categories a {
    padding: 0.53333vw 1.33333vw 0.8vw;
    margin-right: 2.66667vw;
  }
}
